export interface IProfile {
    key: string;
    value: string;
    required?: boolean;
    pattern?: RegExp;
    maxLength?: number;
    minLength?: number;
    label?: { ja: string; en: string };
    inputType?: 'input' | 'textarea' | 'select';
    option?: {
        label: { ja: string; en: string };
        value: string;
    }[];
}

